import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import notification from "../../../../components/notification";
import { Empty, Input, Collapse, Select, Tooltip } from "antd";
import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/tax/tax/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";
import NumberFormat from "../../../App/NumberFormat";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class Tax extends Component {
  state = {
    filter: {
      id_account_sale: [],
      id_account_purchase: [],
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getTaxes } = this.props;
    getTaxes();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.tax.error) {
      notification("error", this.props.tax.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.tax.success) {
      notification("success", this.props.tax.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getTaxes } = this.props;
    this.setState({
      filter: {
        id_account_sale: [],
        id_account_purchase: [],
        pagination: null
      }
    });
    getTaxes(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getTaxes } = this.props;
    this.setState({
      filter: {
        id_account_sale: [],
        id_account_purchase: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getTaxes();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getTaxes } = this.props;
    //const { current_page } = this.props.tax;
    const { filter } = this.state;
    let string = "";

    if (filter.id_account_sale) {
      string += "id_account_sale=" + filter.id_account_sale;
    }
    if (filter.id_account_purchase) {
      string
        ? (string += "&id_account_purchase=" + filter.id_account_purchase)
        : (string += "id_account_purchase=" + filter.id_account_purchase);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getTaxes(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getTaxes } = this.props;
    this.setState({
      filter: {
        id_account_sale: [],
        id_account_purchase: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getTaxes();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateTax(id);
    } else {
      this.props.deactivateTax(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getTaxes } = this.props;
    const { search } = this.props.tax;
    const { filter } = this.props.tax;
    getTaxes(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_tax",
    rowKey: "id_tax",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="taxes.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="taxes.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    // console.log(this.state.filter);
    const // Definisi kolom2 serta field2 API yang dipakai tabel
      columns = [
        {
          title: getLanguage({ id: "name" }),
          dataIndex: "tax_name",
          rowKey: "tax_name",
          width: "10%",
          render: text => <span>{text}</span>
        },
        {
          title: getLanguage({ id: "taxes.add.percentage" }),
          dataIndex: "percentage",
          rowKey: "percentage",
          width: "5%",
          align: "center",
          render: text => <NumberFormat value={text * 100} suffix={"%"} />
          //render: text => <span>{text * 100}%</span>
        },
        {
          title: getLanguage({ id: "salesAccount" }),
          dataIndex: "account_sale",
          rowKey: "account_sale",
          width: "10%",
          align: "center",
          render: (text, row) =>
            text === null ? (
              <span> - </span>
            ) : (
              <span>{text.account_code + " (" + text.account_name + ")"}</span>
            )
        },
        {
          title: getLanguage({ id: "taxes.add.purchaseAccount" }),
          dataIndex: "account_purchase",
          rowKey: "account_purchase",
          width: "10%",
          align: "center",
          render: (text, row) =>
            text === null ? (
              <span> - </span>
            ) : (
              <span>{text.account_code + " (" + text.account_name + ")"}</span>
            )
        },
        // {
        //   title: "Default Sales",
        //   dataIndex: "default_sale",
        //   rowKey: "default_sale",
        //   width: "5%",
        //   align: "center",
        //   render: text => {
        //     let className;
        //     let display;
        //     if (text === 1) {
        //       className = "primary";
        //       display = "Yes";
        //     } else {
        //       className = "nonactive";
        //       display = "No";
        //     }
        //     return <StatusTag className={className}>{display}</StatusTag>;
        //   }
        // },
        // {
        //   title: "Default Purchase",
        //   dataIndex: "default_purchase",
        //   rowKey: "default_purchase",
        //   width: "5%",
        //   align: "center",
        //   render: text => {
        //     let className;
        //     let display;
        //     if (text === 1) {
        //       className = "primary";
        //       display = "Yes";
        //     } else {
        //       className = "nonactive";
        //       display = "No";
        //     }
        //     return <StatusTag className={className}>{display}</StatusTag>;
        //   }
        // },
        {
          title: getLanguage({ id: "status" }),
          dataIndex: "is_active",
          rowKey: "is_active",
          width: "10%",
          align: "center",
          render: text => {
            let className;
            let display;
            if (text === 1) {
              className = "active";
              display = getLanguage({ id: "active" });
            } else {
              className = "nonactive";
              display = getLanguage({ id: "inactive" });
            }
            return <StatusTag className={className}>{display}</StatusTag>;
          }
        }
      ];

    // Data setup
    const { taxes } = this.props.tax;
    // console.log(this.props.tax); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Tax") && columns.length === 5) {
      columns.push(this.listAction);
    }

    Object.keys(taxes).map((tax, index) => {
      return dataSource.push({
        ...taxes[tax],
        key: tax
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.tax;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="taxes.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Tax") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="taxes.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={getLanguage({ id: "name" })}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="taxes.add.salesAccount" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_account_sale"
                      )}
                      value={
                        this.state.filter.id_account_sale
                          ? this.state.filter.id_account_sale
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="adjustmentTypes.filter.select.account" />
                      }
                      loading={this.props.tax.loading}
                      allowClear={true}
                      optionFilterProp="children"
                    >
                      {this.props.tax.accounts.map((data, key) =>
                        data.id_account_type === "10" ? (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  </div>
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="taxes.add.purchaseAccount" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_account_purchase"
                      )}
                      value={
                        this.state.filter.id_account_purchase
                          ? this.state.filter.id_account_purchase
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="adjustmentTypes.filter.select.account" />
                      }
                      loading={this.props.tax.loading}
                      allowClear={true}
                      optionFilterProp="children"
                    >
                      {this.props.tax.accounts.map((data, key) =>
                        data.id_account_type === "05" ? (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  </div>
                </div>
                <div
                  className="isoFilterContent"
                  style={{ paddingTop: "10px" }}
                >
                  <div style={{ width: "15%" }}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "70%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      // allowClear={true}
                      // showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </div>

                  <div
                    style={{
                      width: "20%",
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </div>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {taxes.length === 0 && !this.props.tax.loading ? (
              <Empty description={<IntlMessages id="taxes.table.noData" />} />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.tax.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    tax: state.tax,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(Tax);
